@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
html {
  font-size: 10px;
  font-family: "Montserrat", sans-serif;
  overflow-y: scroll;
}

* {
  margin: 0;
  padding: 0;
}

#root {
  --toastify-color-progress-light: linear-gradient(to right, #bf953f, #fcf6ba, #b38728, #fbf5b7, #aa771c);
}

.vault-item{width:100%;display:grid;grid-auto-flow:column;grid-template-columns:1fr 3fr 3fr 3fr 3fr;justify-content:center;align-items:center;position:relative}.vault-item .vault-item__element{margin:0 10px}.vault-item__details{max-height:0;transition:max-height .4s ease-out;overflow:hidden;height:200px}.vault-item__details--show{overflow:hidden;height:200px;max-height:500px;transition:max-height .4s ease-in}
.vault-header{margin-top:4rem;width:110rem;height:30px;display:grid;grid-auto-flow:column;grid-template-columns:1fr 3fr 3fr 3fr 3fr;align-items:center;place-items:center;font-size:1.5rem;font-weight:bold}
.Vaults-container{display:flex;flex-direction:column;align-items:center;height:100vh}
.vault-item{width:100%;display:grid;grid-auto-flow:column;grid-template-columns:1fr 3fr 3fr 3fr 3fr;justify-content:center;align-items:center;position:relative}.vault-item .vault-item__element{margin:0 10px}.vault-item__details{max-height:0;transition:max-height .4s ease-out;overflow:hidden;height:200px}.vault-item__details--show{overflow:hidden;height:200px;max-height:500px;transition:max-height .4s ease-in}
.btn{background-color:#fffcff;border-radius:4px;border:1px solid #50514f;color:#50514f;cursor:pointer;display:inline-block;font-size:14px;font-weight:bold;line-height:1.42857143;margin:20px;padding:.5em 1em;text-align:center;text-decoration:none;transition:all .3s ease-in-out;-webkit-user-select:none;-ms-user-select:none;user-select:none;vertical-align:middle}.btn:hover{background-color:#fffcff;box-shadow:10px 10px 0 -2px #e0f134,10px 10px 0 0 #000;color:#50514f;-webkit-transform:translate3d(-2px, -2px, 0);transform:translate3d(-2px, -2px, 0);-moz-transform:translate3d(-2px, -2px, 0)}.btn:focus{outline:none}.btn:active{background-color:#fffcff;box-shadow:8px 8px 0 -1px #e0f134,8px 8px 0 0 #000;color:#50514f;-webkit-transform:translate3d(0px, 0px, 0);transform:translate3d(0px, 0px, 0);-moz-transform:translate3d(0px, 0px, 0);transition:all .1s ease-in-out}.btn:disabled{background-color:#fffcff;color:#50514f}
#App{width:100%;height:100%;overflow-x:hidden;background-position:center;background-size:cover;background-repeat:no-repeat}.authenticated-container{display:flex;flex-direction:column;align-items:center;height:100vh}
